<template>
  <div class="operation-maintenance-monitor">
    <div class="grid-container">
      <el-table
        border
        stripe
        ref="table"
        :data="grid.rows"
        :height="grid.height"
        highlight-current-row
        v-loading="grid.loading"
        @selection-change="val => grid.selectedRow =val"
      >
        <el-table-column type="index" width="50" align="center" label="序号"></el-table-column>
        <el-table-column prop="typeName" label="运维监控参数" align="left" show-overflow-tooltip></el-table-column>
        <el-table-column
          width="200"
          align="left"
          prop="frequency"
          :formatter="formatFrequency"
          label="采集频率"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          width="200"
          align="left"
          prop="threshold"
          :formatter="formatThreshold"
          label="报警阈值"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          width="200"
          align="left"
          prop="maxDuration"
          :formatter="formatMaxDuration"
          label="报警未处理最大时长"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column prop="userName" label="编制人" align="left" width="200" show-overflow-tooltip></el-table-column>
        <el-table-column
          width="150"
          align="center"
          label="编制日期"
          prop="createTime"
          show-overflow-tooltip
        >
          <template slot-scope="scope">{{scope.row.createTime | dateTime('YYYY-MM-DD')}}</template>
        </el-table-column>
        <el-table-column align="center" width="100" label="操作">
          <template slot-scope="scope">
            <el-link
              style="font-size: 20px;margin: 0 5px 0 0;"
              icon="el-icon-edit-outline"
              @click="onUpdate(scope.row)"
            ></el-link>
            <el-link
              style="font-size: 20px;margin: 0 5px 0 0;"
              icon="el-icon-view"
              @click="onView(scope.row)"
            ></el-link>
            <!--            <el-link-->
            <!--              style="font-size: 20px;margin: 0 5px 0 0;"-->
            <!--              icon="el-icon-delete"-->
            <!--              @click="onDelete(scope.row)"-->
            <!--            ></el-link>-->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      v-dialog
      width="700px"
      v-if="dialog.show"
      :visible.sync="dialog.show"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
    >
      <div slot="title" class="title">
        <i :class="dialogMode.icon"></i>
        <span>运维监控参数 —— {{dialogMode.title}}</span>
      </div>
      <el-form ref="form" :rules="dialog.rules" :model="dialog.data" label-width="110px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="参数类型" prop="subName">
              <el-input disabled v-model="dialog.data.typeName"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="采集频率" prop="threshold0" label-width="130px">
              <el-input :disabled="dialog.mode===3 || dialog.data.threshold0 === '无'" v-model="dialog.data.threshold0"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="单位" prop="unit0">
              <el-select :disabled="dialog.mode===3" v-model="dialog.data.unit0" style="width: 100%;">
                <el-option v-for="item in units" :key="item.value" :value="item.value" :label="item.label"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="报警阈值" prop="threshold1" label-width="130px">
              <el-input :disabled="dialog.mode===3 || dialog.data.threshold1 === '无'" v-model="dialog.data.threshold1"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="单位" prop="unit1">
              <el-input disabled v-model="dialog.data.unit1"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="未处理最大时长" prop="threshold2" label-width="130px">
              <el-input :disabled="dialog.mode===3 || dialog.data.threshold2 === '无'" v-model="dialog.data.threshold2"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="单位" prop="unit2">
              <el-select :disabled="dialog.mode===3" v-model="dialog.data.unit2" style="width: 100%;">
                <el-option v-for="item in units" :key="item.value" :value="item.value" :label="item.label"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="编制人" prop="userName">
              <el-input disabled v-model="dialog.data.userName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="编制时间" prop="createTime">
              <el-date-picker
                disabled
                v-model="dialog.data.createTime"
                type="date"
                placeholder="选择日期"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="onSave" icon="fa fa-save" v-if="dialog.mode !== 3">&nbsp;保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'operation-maintenance-monitor',
  data () {
    const validateThreshold = (rule, value, next) => {
      if (!value && value !== 0) {
        next('请输入阈值！')
      } else if (!/^(-?\d+)$/.test(value)) {
        next('请输入整数！')
      } else if (value <= 0) {
        next('请输入大于0的整数！')
      } else {
        next()
      }
    }
    const validatePercentThreshold = (rule, value, next) => {
      if (!value && value !== 0) {
        next('请输入阈值！')
      } else if (!/^(-?\d+)$/.test(value)) {
        next('请输入整数！')
      } else if (value <= 0) {
        next('请输入大于0的整数！')
      } else if (value > 100) {
        next('请输入小于100的整数！')
      } else {
        next()
      }
    }
    return {
      grid: {
        rows: [],
        height: null,
        loading: false,
        selectedRow: []
      },
      dialog: {
        mode: null,
        show: false,
        data: [],
        rules: {
          threshold0: [
            {
              required: true,
              validator: validateThreshold,
              trigger: 'blur'
            }
          ],
          threshold1: [
            {
              required: true,
              validator: validatePercentThreshold,
              trigger: 'blur'
            }
          ],
          threshold2: [
            {
              required: true,
              validator: validateThreshold,
              trigger: 'blur'
            }
          ]
        }
      },
      units: [{
        label: '小时',
        value: '1'
      }, {
        label: '分钟',
        value: '2'
      }, {
        label: '秒',
        value: '3'
      }]
    }
  },
  computed: {
    dialogMode () {
      switch (this.dialog.mode) {
        case 1:
          return { title: '新增', icon: 'el-icon-document-add', message: '添加成功！' }
        case 2:
          return { title: '编辑', icon: 'el-icon-edit-outline', message: '修改成功！' }
        case 3:
          return { title: '查看', icon: 'el-icon-document' }
        default:
          return {}
      }
    }
  },
  created () {
    this.featchData()
  },
  methods: {
    /**
     格式化 表格数据
     */
    formatTime: function (time, unit) {
      if (time === '无') {
        return '无'
      }
      if (unit === '1') {
        return time / 3600 + '(小时)'
      } else if (unit === '2') {
        return time / 60 + '(分钟)'
      } else if (unit === '3') {
        return time + '(秒)'
      } else {
        return time + '%'
      }
    },
    formatFrequency: function (row) {
      if (!row.threshold0) {
        row.threshold0 = '无'
      }
      // 值 + 单位
      return this.formatTime(row.threshold0, row.unit0)
    },
    formatThreshold: function (row, column) {
      if (!row.threshold1) {
        row.threshold1 = '无'
      }
      return this.formatTime(row.threshold1, row.unit1)
    },
    formatMaxDuration: function (row) {
      if (!row.threshold2) {
        row.threshold2 = '无'
      }
      return this.formatTime(row.threshold2, row.unit2)
    },
    /**
     获取 主页面表格数据
     */
    async featchData () {
      this.grid.loading = true
      let { data } = await this.$api.queryData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.ThresholdMapper.groupThresholds',
          typeCodes: JSON.stringify(['01', '02', '03', '05', '06', '07', '08'])
          // appId: $store.state.system.appId
        }
      })
      this.grid.loading = false
      data.data.forEach((item) => {
        if (item.status === 1) {
          item.status = true
        }
      })
      // 整理数据
      let rows = []
      let rowIndex
      data.data.forEach((item, index) => {
        rowIndex = Number(item.typeCode)
        if (rowIndex > 3) rowIndex--
        if (!rows[rowIndex]) {
          rows[rowIndex] = {
            typeName: null,
            typeCode: null,
            thresholds: [],
            createTime: null,
            userName: null
          }
        }
        rows[rowIndex].typeName = item.typeName
        rows[rowIndex].typeCode = item.typeCode
        rows[rowIndex].createTime = item.createTime
        rows[rowIndex].userName = item.userName
        rows[rowIndex]['subCode' + (Number(item.subCode) - 1)] = item.subCode
        rows[rowIndex]['subName' + (Number(item.subCode) - 1)] = item.subName
        rows[rowIndex]['threshold' + (Number(item.subCode) - 1)] = item.threshold
        rows[rowIndex]['unit' + (Number(item.subCode) - 1)] = item.unit
        rows[rowIndex].unit1 = '%'
      })
      this.grid.rows = rows
    },
    /**
     输入数字合法性 校验
     */
    async validateNumeric () {
      let regPos = /^(-?\d+)$/ // 浮点数
      let thresholds = []
      thresholds.push({
        threshold: this.dialog.data.threshold0,
        unit: this.dialog.data.unit0
      }, {
        threshold: this.dialog.data.threshold1,
        unit: this.dialog.data.unit1
      }, {
        threshold: this.dialog.data.threshold2,
        unit: this.dialog.data.unit2
      })
      for (let i = 0; i < 3; i++) {
        let item = thresholds[i]
        if (item.threshold !== '无' && !regPos.test(item.threshold)) {
          this.$message.info('指标阈值必须为整数！')
          return Promise.reject(new Error())
        }
        if (item.threshold !== '无' && item.threshold < 0) {
          this.$message.info('指标阈值不能＜0！')
          return Promise.reject(new Error())
        }
        if (!item.unit) {
          this.$message.info('必须选择单位！')
          return Promise.reject(new Error())
        }
      }
      if (this.dialog.data.threshold1 !== '无' && this.dialog.data.threshold1 > 100) {
        this.$message.info('指标阈值必须＜100%！')
        return Promise.reject(new Error())
      }
      return Promise.resolve()
    },
    /**
     查看对话框 初始化
     */
    onView (row) {
      this.dialog.mode = 3
      this.dialog.show = true
      this.dialog.data = Object.assign({}, row)
    },
    /**
     更新对话框 初始化
     */
    async onUpdate (row) {
      try {
        this.dialog.mode = 2
        this.dialog.show = true
        this.dialog.data = Object.assign({}, row)
        this.dialog.data.threshold0 = this.transformSecond(this.dialog.data.threshold0, this.dialog.data.unit0, 0)
        this.dialog.data.threshold2 = this.transformSecond(this.dialog.data.threshold2, this.dialog.data.unit2, 0)
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    },
    /**
     保存前 校验
     */
    async validateBeforeSave () {
      await this.$refs['form'].validate()
      await this.validateNumeric()
      if (this.dialog.mode === 2) {
        // 编辑保存前校验
      }
      return Promise.resolve()
    },
    transformSecond (time, unit, dir) {
      if (time === '无') {
        return null
      }
      // 正向 =》秒
      if (dir === 1) {
        if (unit === '1') {
          return time * 3600
        } else if (unit === '2') {
          return time * 60
        } else if (unit === '3') {
          return time
        }
      } else {
        if (unit === '1') {
          return time / 3600
        } else if (unit === '2') {
          return time / 60
        } else if (unit === '3') {
          return time
        }
      }
    },
    /**
      保存 操作
     */
    async onSave () {
      try {
        await this.validateBeforeSave()
        this.dialog.loading = true
        let model = this.dialog.data
        model.threshold0 = this.transformSecond(model.threshold0, model.unit0, 1)
        model.unit1 = ''
        model.threshold2 = this.transformSecond(model.threshold2, model.unit2, 1)
        await this.$api.modify({
          data: {
            mapperId: 'com.bosssoft.monitor.dao.ThresholdMapper.batchUpdateByPrimaryKeySelective',
            ...model
          }
        })
        this.dialog.loading = false
        this.featchData()
        this.dialog.show = false
        this.$message.success(this.dialogMode.message)
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .operation-maintenance-monitor {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .grid-container {
      flex: 1;
    }
  }
</style>
